import request from './request.js'


//获取所有一级教程标题
export function getAllTutorialsForOneSubject() {
    return request({
        method: 'GET',
        url: '/tutorial/first'
    })
}

//获取相应二级教程标题
export function getAllTutorialsForTwoSubject(titleNum) {
    return request({
        method: 'GET',
        url: `/tutorial/second/${titleNum}`
    })
}

//获取某个特定教程的详情信息
export function getTutorialById(tutoId) {
    return request({
        method: 'GET',
        url: `/tutorial/view?tutoId=${tutoId}`
    })
}

//创建评论
export function addComment(data) {
    return request({
        method: 'POST',
        url: '/comment',
        data
    })
}

//根据id获取评论
export function getCommentByTutorialId(data) {
    const { tutoId, page, pageSize } = data;
    console.log("请求参数", data);
    return request({
        method: 'GET',
        url: `/tutorial/${tutoId}/commentList/${page}/${pageSize}`
    })
}