<template>
    <div class="comment-item">
        <div class="main-part">
            <div class="userinfo-box">
            <img class="avatar" :src="'http://'+commentItem.commentAvatar" alt="">
            <div class="user-name">
                 {{commentItem.commentUserId}}
            </div>
            </div>
            <div class="comment-content">
                <p> {{commentItem.content}}
                </p>
            </div>
        </div>
        <div class="reply" @click="ifShow=!ifShow">
            <span>回复</span>
        </div>

      <transition name="el-zoom-in-top">
        <div class="develop-reply-comment" v-show="ifShow">
            <el-input
            class="comment-content-area"
            type="textarea"
            :rows="2"
            placeholder="发表一条友善的回复"
            v-model="reply">
            </el-input>
            <div class="reply-button" @click="developReply()">
                发表<br>回复
            </div>
        </div>
        </transition>

        <div class="reply-item" v-for="item in commentItem.replyList" :key='item.commentId'>
            <div class="main-part">
                <div class="userinfo-box">
                    <img class="avatar" :src="'http://'+item.commentAvatar" alt="">
                    <div class="user-name">
                        {{item.commentUserId}}                       
                    </div>
                </div>
                <div class="reply-content">
                    <div class="reply-to">
                        回复@{{commentItem.commentUserId}}
                    </div>
                    <p>
                        {{item.content}}
                    </p>
                </div>
           </div>
           <div class="reply" @click="replyRshow(item.commentId)">
               <span>回复</span>
            </div>
            <transition name="el-zoom-in-top">
           <div class="develop-reply-comment" v-show="ifShow2&&(currentReply==item.commentId)">
            <el-input
            class="comment-content-area"
            type="textarea"
            :rows="2"
            placeholder="发表一条友善的回复"
            v-model="reply">
            </el-input>
            <div class="reply-button" @click="developReplyTwo(item.commentId)">
                发表<br>回复
            </div>
        </div>
        </transition>
        </div>  
    </div>
</template>

<script>
import {addComment} from '../network/tutorial.js'
export default {
    props:{
       commentItem:{
           type:Object
       }
    },
    created(){
         this.getUserInfo();
    },
    data(){
        return{
            reply:'',
            ifShow:false,
            ifShow2:false,
            reply:'',
            replyList:[],
            userInfo:null,
            currentReply:''
        }
    },
    methods:{
        async developReply()
        {
            // this.replyList.push(this.reply);
            // this.ifShow=false;
            // this.$message({
            //     type:'success',
            //     message:'发表回复成功!'
            // })
            // this.reply='';

             let data={
               commentTutoId:this.commentItem.commentTutoId,
               commentUserId:this.userInfo.userId,
               content:this.reply,
               replyTo:this.commentItem.commentId,
              }
              console.log('发表回复',data);
              let res=await addComment(data);
              console.log(res);
              this.ifShow=false;
              this.$emit('replyEmit');
              this.reply='';

        },
        async developReplyTwo(commentId){
           console.log(commentId);
           let data={
               commentTutoId:this.commentItem.commentTutoId,
               commentUserId:this.userInfo.userId,
               content:this.reply,
               replyTo:commentId,
              }
              console.log('发表回复',data);
              let res=await addComment(data);
              console.log("回复了回复",res);
              this.ifShow2=false;
              this.$emit('replyEmit');
              this.reply='';
              this.$message({
                  type:'success',
                  message:'发表回复成功'
              })
        },
        getUserInfo(){
           let userInfo=localStorage.getItem('userInfo');
           this.userInfo=JSON.parse(userInfo);
        
        },
        replyRshow(commentId)
        {
              this.ifShow2=!this.ifShow2;
              this.currentReply=commentId;
        }
    }
}
</script>

<style scoped>
    .comment-item
    {
        width: 100%;
        padding: 10px;
        border-radius: 10px;
        border:1px solid #eee;
        margin-bottom: 20px;
    }
    .avatar
    {
        width: 40px;
        height: 40px;
        border-radius: 50%;
    }
    .userinfo-box
    {
        width: 60px;
        text-align: center;
    }
    .user-name
    {
        font-size: 13px;
    }

    .main-part
    {
        display: flex;
    }
    .comment-content,.reply-content
    {
        padding: 10px;
        font-size: 15px;
    }
    .reply
    {
        display: flex;
        justify-content: flex-end;
        color:#D893F5;
        font-size:10px;
        padding: 0 10px;
    }
    .reply span:hover
    {
        color:#BB43F9;
        cursor: pointer;
    }
    .reply-item
    {
        padding-left: 60px;
    }
    .reply-to
    {
        color:#D893F5;
    }

     .reply-button
    {
        border-radius: 10px;
        background-color: #D893F5;
        font-size:15px;
        font-weight: 700;
        color:white;
        height: 50px;
        width: 50px;
        margin-left: 10px;
        text-align: center;
        line-height: 25px;
        transition: .6s;
    }
    .reply-button:hover
    {
        background-color:#BB43F9;
        cursor: pointer;
    }
    .develop-reply-comment
    {
        display: flex;
        padding:10px;
    }
</style>