<template>
    <div class="tutorial-page">
        <!-- <form action="http://8.135.61.132/cts/user/register" method="post">
            <input type="text" name='userId'> 
            <input type="text" name='password'>
            <input type="submit" value='提交'>
        </form> -->
        <div id="tutorial-container">
            <!-- 左侧部分 -->
             <div class="left-part">
                 <div class="tutorial-title">
                     教程目录
                 </div>
                 <el-tree
                 class="tutorial-tree"
                :data="tutorialList"
                :props="defaultProps"
                accordion
                @node-click="handleNodeClick">
                </el-tree>
             </div>
             <!-- 左侧部分结束 -->
             <!-- 右侧部分开始 -->
             <div class="right-part" v-if="ifTutorial">
                 <!-- 教程部分展示 -->
                 <div  v-if="currentTutorial!=null">
                     <div class="tutorial-detail-title">
                    <h1>教程{{tutorialInfo.titleNum}}:{{tutorialInfo.titleContent}}</h1>
                   </div>
                   <div class="tutorial-detail-content" v-html="tutorialInfo.content">
                     {{tutorialInfo.content}}
                 </div>
                 </div>
                 
                 <div class="null" v-else>
                     <div class="welcome">欢迎来到码到成功平台！</div>
                     <img :src="require('../../static/image/index.jpg')" alt="" id="index">
                 </div>
                 <!-- 教程部分结束 -->
                  
                  <!-- 练习题部分 -->
                <div class='other-container' v-if="currentTutorial!=null"> 
                <div class="problem-title">
                    <h2>相关练习题</h2>
                </div>
                <div>
                <el-table
                    id="problem-table"
                    :data="tutorialInfo.problemList"
                    stripe
                    style="width: 100%">
                    <el-table-column
                    prop="probId"
                    label="题目ID"
                    width="180">
                    </el-table-column>
                    <el-table-column
                    prop="probTitle"
                    label="题名"
                    >
                    </el-table-column>
                    <el-table-column
                    label="题解"
                    width="180">
                    <template slot-scope="scope">
                        <el-button
                        type="success"
                        @click="toAnswer(scope.$index, scope.row)"
                        >查看题解</el-button>
                    </template>
                    </el-table-column>
                    <el-table-column
                    label="练习"
                    width="180">
                     <template slot-scope="scope">
                        <el-button
                        type="primary"
                        @click="toProblem(scope.$index, scope.row)"
                        >去练习</el-button>
                    </template>
                    </el-table-column>
                </el-table>
                </div>
                 <!-- 评论部分展示 -->
                 <div class="comment-list">
                     <div class="comment-title">
                         评论列表
                     </div>
             
                     <div class="develop-comment">
                        <div class="userinfo-box">
                            <img class=avatar :src="userInfo.avatar" alt="">
                            <div class="user-name">{{userInfo.userId}}</div>
                        </div>
                        <el-input
                        class="comment-content-area"
                        type="textarea"
                        :rows="2"
                        placeholder="发表一条友善的评论"
                        v-model="comment">
                        </el-input>
                        <div class="comment-button" @click="developComment()">
                            发表<br>评论
                        </div>
                     </div>

                     <comment-item v-for="item in tutorialInfo.commentList"
                     :key="item.commentId"
                     :commentItem="item"
                     @replyEmit="replyEmit"></comment-item>
                     
                     <div class="page-box">
                         <el-pagination
                        background
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="pageInfo.page"
                        :page-sizes="[10, 20, 30, 40]"
                        :page-size="pageInfo.pageSize"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="this.pageInfo.dataTotal">
                      </el-pagination>
                     </div>                     
                 </div>
                 <!-- 评论部分结束 -->
             </div>

                </div>
               

             <div class="right-part" v-if="!ifTutorial">
             </div>
             <!-- 右侧部分结束 -->
        </div>        
    </div>
</template>

<script>
import CommentItem from '../CommentItem.vue'
