            },
            {
                probId:2,
                probTitle:'黑熊过河',
                content:'黑熊过河'
            }
        ],

        },
        totalCommentList:[

        ],
        primaryCommentList:[

        ]

        }
    },
    methods: {
        //测试网络请求
        async testNetwork()
        {
           let res=await testNetwork();
           console.log(res);
        },
        //处理目录点击
        handleNodeClick(e)
        {
            // console.log(e);
            //存在index，一级目录
            // console.log(e.index)
