            this.currentTutorial=e.tutoId;
            this.getTutorialById();
        },
        //当前页条数改变
        handleSizeChange(val) {
            this.pageInfo.pageSize=val;
            this.pageComment();
        },
        //当前页改变
        handleCurrentChange(val) {
            this.pageInfo.page=val;
            this.pageComment();
        },
        //发表评论
        async developComment()
        {
        
           //构造data对象
           let data={
               commentTutoId:this.currentTutorial,
               commentUserId:this.userInfo.userId,
               content:this.comment,
               replyTo:null,
           }
           console.log('发表评论');
           let res=await addComment(data);
           this.getTutorialById();
           
        //    this.getTutorialById();
        //    this.getCommentById();
        //    this.$message({
        //      message: '发表评论成功!',
        //     type: 'success'
        //    })
        //    console.log(res);
           //再次获取评论数据
        //    let data1={
        //        tutoId:this.currentTutorial,
        //        page:this.pageInfo.page,
        //        pageSize:this.pageInfo.pageSize
        //    }
        //    console.log(data1);
        //    let res1=await getCommentByTutorialId(data1);
        //    console.log(data1);
        //    console.log(res1);
           //更新分页器对象以及教程对象
            // this.$turn(this.pageInfo,res1.data.data);
        //   this.$turn(this.tutorialInfo.res1.data.data);
            //  this.tutorialInfo.commentList=res1.data.data.commentList;
        //    //向后台增加评论
            this.$message({
           message: '发表评论成功!',
           type: 'success'
           });
             this.comment='';
        },
