        {
            console.log(index,row);
        },
        //去往练习
        toProblem(index,row)
        {
            let string=encodeURIComponent(JSON.stringify(row));
            this.$router.push({
                path:`/index/problem/${string}`
            });
        },
        //去往题解
        toAnswer(index,row)
        {
            let probId=row.probId;
            this.$router.push(`/index/answer/${probId}`)
        },
        //根据id获取教程
        async getTutorialById()
        {
           let res=await getTutorialById(this.currentTutorial);
        //    this.objectTurn(this.tutorialInfo,data);
           console.log("教程详情",res);
           let data=res.data.data;
           this.$turn(this.tutorialInfo,data);
           console.log(data.commentList);
           this.primaryCommentList=data.commentList;
           this.tutorialInfo.commentList=this.tutorialInfo.commentList.filter(v=>
               v.replyTo==null)
           
        //    let newCommentList=[];
        //    for(let item of this.tutorialInfo.commentList)
        //    {
        //        if(item.replyTo.replyTo==null)
        //        newCommentList.push(item);
        //    }
        //    console.log("xx",newCommentList);
        //    this.tutorialInfo.commentList=newCommentList;
           console.log("过滤后",this.tutorialInfo.commentList);
            for(let item of this.tutorialInfo.commentList)
            {
                item.commentTutoId=this.currentTutorial;
                item.replyList=[];
                for(let item1 of data.commentList)
                {
                    if(item1.replyTo==item.commentId)
                    item.replyList.push(item1);
                }
                    // for(let item1 of this.tutorialInfo.commentList)
                    // {
                    //      if(item1.replyTo==item.commentId)
                    //      item.replyList.push(item1);
                    // }
            }
            for(let item of this.tutorialInfo.commentList)
            {
                for(let item1 of item.replyList)
                {
                    console.log("总量",this.primaryCommentList);
                    let list=this.primaryCommentList.filter(v=>v.replyTo==item1.commentId);
                    console.log("回复的回复",list);
                    item.replyList=[...item.replyList,...list];                   
                }
            }
            this.totalCommentList=this.tutorialInfo.commentList;
            this.pageComment();
            console.log("评论s",this.tutorialInfo.commentList);
        //    console.log(data);
        //    for(let item of data.commentList)
        //    {
        //        if(item.replyTo!=null)
        //        continue;
        //        let replyList=data.commentList.filter(v=>v.replyTo==item.commentUserId);
        //        item.replyList=replyList;
        //    }
            //再次获取评论数据
        //    let data1={
        //        tutoId:this.currentTutorial,
        //        page:this.pageInfo.page,
        //        pageSize:this.pageInfo.pageSize
        //    }
        //    let res1=await getCommentByTutorialId(data1);
        //    console.log("res1",res1);
        //    console.log(this.tutorialInfo.commentList);
        //    this.tutorialInfo.commentList=res1.data.data.commentList;
        //    await this.getCommentById();
        //    this.commentList=data.commentList;
        //    console.log("评论列表",this.commentList);
           console.log(data.problemList);
           this.tutorialInfo.problemList=data.problemList;
           for(let item of this.tutorialInfo.problemList)
           {
              let index=item.content.indexOf('</h4>');
              item.probTitle=item.content.substring(4,index);
           }
           console.log("问题列表",this.tutorialInfo.problemList);
           this.$message({
           message: '加载数据成功!',
           type: 'success'
           });
        },
        //获取所有一级标题
        async getAllTutorialsForOneSubject()
        {
            let res=await getAllTutorialsForOneSubject();
            let data=res.data.data;
            let titleContentList=data.titleContentList;
            //将lable换成titleNum-titleContent的形式
            // console.log(titleContentList);
            let list=[];
            for(let item of titleContentList)
            {               
                let obj={
                   label:`${item.titleNum}-${item.titleContent}`,
                   tutoId:item.tutoId,
                   index:this.tutorialList.length
                }
                await this.getAllTutorialsForTwoSubject(obj);
                list.push(obj);                
            }
            this.tutorialList=list;
            console.log("list",this.tutorialList);
        },
        getUserInfo()
        {
           //获取用户信息
           let userInfo=JSON.parse(localStorage.getItem("userInfo"));
           this.userInfo=userInfo;
           console.log(this.userInfo);
        },
        //获取相应的二级标题
        async getAllTutorialsForTwoSubject(obj)
        {
             let res=await getAllTutorialsForTwoSubject(obj.tutoId);
             let data=res.data.data;
             let titleContentList=data.titleContentList;
             let childrenList=[];
             for(let item of titleContentList)
             {
                childrenList.push({
                    label:`${item.titleNum}-${item.titleContent}`,
                    tutoId:item.tutoId,
                })
             }
            //  console.log(childrenList);
             obj.children=childrenList;
        },
        async testInterface()
        {
            
        },
        async getCommentById()
        {
            let obj={
                tutoId:this.currentTutorial,
                page:this.pageInfo.page,
                pageSize:this.pageInfo.pageSize
            }
            console.log('请求',obj);
            let data=await getCommentByTutorialId(obj);
            this.tutorialInfo.commentList=data.data.data.commentList;
            for(let item of this.tutorialInfo.commentList)
            {
                item.commentTutoId=this.currentTutorial;
                if(item.replyTo==null)
                {
                    item.replyList=[];
                    for(let item1 of this.tutorialInfo.commentList)
                    {
                         if(item1.replyTo==item.commentId)
                         item.replyList.push(item1);
                    }
                }
            }
            console.log("评论",this.tutorialInfo.commentList);
            this.$turn(this.pageInfo,data.data.data);
        },
        //回复触发再次获取评论列表
        replyEmit()
        {
            this.getTutorialById();
        },
        pageComment()
        {
            //分页
            console.log(this.totalCommentList);
            this.pageInfo.dataTotal=this.totalCommentList.length;
            this.pageInfo.pageTotal=this.pageInfo.dataTotal/this.pageInfo.pageSize;
            let newCommentList=[];
            let start=(this.pageInfo.page-1)*this.pageInfo.pageSize;
            console.log(start);
            let border=this.pageInfo.dataTotal;
            border=((start+this.pageInfo.pageSize)>border?border:(start+this.pageInfo.pageSize));
            console.log("border",border);
            for(let i=start;i<border;i++)
            {
                newCommentList.push(this.totalCommentList[i]);
            }
            this.tutorialInfo.commentList=newCommentList;
            console.log("新的",this.tutorialInfo.commentList);
        }
        // async testEditor()
        // {

        //     let editor=new CppEditor(this.$refs['codePart']);
        //     await editor.render();
        //     editor.setCode("#include<string>");
        //     console.log(editor.getCode());
        // }
    },
     created(){
        //  let html=this.$showdown.makeHtml();
        //  console.log(html);
        //  this.tutorialInfo.content=html;
        //  this.testNetwork();
        this.getAllTutorialsForOneSubject();
        this.getUserInfo();
        },
    mounted(){
        //  this.testEditor();
    }
}
</script>

<style scoped>
    .code-part
    {
        width: 100%;
        height: 400px;
    }
    #tutorial-page
    {
        background-color: #fff;
        width: 100%;
        height: 100%;
           
    }
    #tutorial-container
    {
        width: 100%;
        display: flex;
        padding-top:20px;
        height: 100%;
        color:#909399;  
    }
    .left-part
    {
       flex:3;
       height: 640px;
       border-right:3px solid #eee;
       padding-left:20px;
    }
    .right-part
    {
       padding:0 20px;
       flex:8;
       height: 640px;
       overflow:scroll;
    }
    .tutorial-title
    {
        width: 100%;
        padding-left: 20px;
        color:var(--font_color);
        padding-bottom: 20px;
    }
    .tutorial-tree
    {
        font-size:20px;
        color:#909399;
    }
    .develop-comment
    {
        padding:35px 0;
        display: flex;
        border-bottom: 1px solid #eee;
    }
    .tutorial-detail-title,.comment-title,.problem-title
    {
        width: 100%;
        height: 50px;
        line-height: 50px;
        border-bottom: 1px solid #eee;
    }
    .comment-list
    {
        margin-top:20px;
    }
    .comment-title
    {
        font-size:23px;
    }
    .userinfo-box
    {
        width: 60px;
        text-align: center;
    }
    .avatar
    {
        width: 40px;
        height: 40px;
        border-radius: 50%;
    }
    .user-name
    {
        font-size:13px;
    }
    .comment-button
    {
        border-radius: 10px;
        background-color: #D893F5;
        font-size:15px;
        font-weight: 700;
        color:white;
        height: 50px;
        width: 50px;
        margin-left: 10px;
        text-align: center;
        line-height: 25px;
        transition: .6s;
    }
    .comment-button:hover
    {
        background-color:#BB43F9;
        cursor: pointer;
    }
    .comment-content-area
    {
        width: 850px;
    }
    .page-box
    {
        height: 90px;
        width: 100%;
        position: relative;
    }
    .el-pagination
    {
        position: absolute;
        left: 50%;
        top:50%;
        transform: translate(-50%,-50%);
    }
    .tutorial-detail-content
    {
        line-height: 40px;
        padding:10px 0;
    }
    #index
    {
         width: 95%;
         border-radius:60px ;
    }
    .welcome
    {
        width: 100%;
        text-align: center;
